div.third {
  @include span-columns(12 of 12);
}

div.three-cols {
  display: block;
  float: left;
  clear: both;
  width: 100%;
}

div.three-cols div.third {
  padding: $main_gutter;
  // text-align: center;

}
div.three-cols div.third a,
div.three-cols div.third .gray-area {
  display: block;
  float: left;
  clear: both;
  width: 100%;
  color: currentColor;
  text-decoration: none;
  position: relative;
  min-height: pem(78);
  background: $color-tab-gray;

  h1 {
    margin-bottom: 0.25em;
    text-transform: uppercase;
  }

  &:hover span {
    text-decoration: underline;
  }
}

div.three-cols hgroup {
  display: block;
  float: left;
  width: 100%;
  position: relative;
  background-color: transparent;
  padding: 0.5em 0.5em 0.25em ($icon-width * 0.88);
  margin-top: 0.8em;
  min-height: $icon-height;
}

div.three-cols hgroup::before {
  // the icon:
  content: "";
  position: absolute;
  left: -$icon-left-overlap;
  top: 0;
  width: $icon-width;
  height: $icon-height;
  background-color: $color-wpca-blue;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 75%;
}

div.three-cols hgroup::after {
  content: "";
  position: absolute;
  left: -$icon-left-overlap;
  top: $icon-height;

  @include triangle(up-right, darken($color-wpca-blue, 25%), $icon-left-overlap);
}


div.three-cols span.blurb {
  display: block;
  float: left;
  clear: none;
  width: 100%;
  padding: 0.5em 0.75em;
  // background-color: rgba(255, 255, 255, 0.4);
}

// training page header:
div.three-cols header.training,
div.three-cols header.training span {
  display: block;
  float: left;
  clear: both;
  width: 100%;
  padding: 0 1em;
}

div.three-cols header.training span {
  background: $color-wpca-blue;
  color: $color-tab-gray;
  border-radius: 25px 25px 0 0;
  text-align: center;

  h2,
  h3 {
    margin-bottom: 0;
    text-transform: uppercase;
  }

  h2 {
    font-size: 4em;
    line-height: 1em;
    padding-top: 0.125em;
  }

  h3 {
    margin-top: -0.25em;
    padding-bottom: 0.5em;
  }

}

// ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++
