span.main-bg {
  padding-bottom: 3.5em;
}


div.content span.single-col {
  text-align: center;
  @include span-columns(8 of 12);
  @include shift(2);
}

div.content h1,
div.content h2 {
  text-align: center;
}
h1.page-title {
  font-size: 2em;
  padding-bottom: 0;
}

img.right,
img.left {
  max-width: 30%;
}


div.page div.texture {
  display: block;
  float: left;
  clear: none;
  width: 100%;
  background-color: #FFF;
  background-image: url('../images/bg-texture-01.png');
}
div.texture > span.main-bg {
  background-color: transparent;
  background-image: radial-gradient(top center, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.15));
}
