.outer {
  display: block;
  @include outer-container;
}



img.right {
  float: right;
  max-width: 40%;
  padding: 0 0 1em 1.5em;
}

img.left {
  float: left;
  max-width: 40%;
  padding: 0 0 1em 1.5em;
}
h1.page-title {
  text-transform: uppercase;
  letter-spacing: 0.07em;
}
div.contact_form {
  text-align: left;
  @include span-columns(12 of 12);
}

div.contact_form {

  span.main,
  footer,
  span.footnote {
    display: block;
    float: left;
    clear: both;
    width: 100%;
  }

  span.main {
    position: relative;
    padding-bottom: 0.5em;
  }

  label {
    width: 100%;
    float: left;
    clear: both;
  }
  span.err {
    float: right;
  }

  strong {
    // required callout
    color: $color-alert-red;
  }
  span.footnote {
    padding-top: 0.25em;
    text-align: center;
  }
  footer {
    text-align: center;
  }
}

div.contact_form div.progress {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.65);
  color: #FFF;

}
