.drugs-cols header.drugs {
  display: block;
  float: left;
  clear: both;
  width: 100%;
  text-align: center;
  vertical-align: center;
  height: 270px;
  line-height: 270px;

  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  margin-bottom: 0.25em;

  font-weight: bold;
  font-size: 2.75em;
}


.drugs-cols header.drugs div {
  // top quantity:
  font-size: smaller;

}




div.three-cols div.absent {

  header.drugs {
    background-image: url('../images/graph-01.svg');
    .no-svg & {
      background-image: url('../images/graph-01.png');
    }
  }

  hgroup::before {
    background-size: 55%;
    background-image: url('../images/icon-paint-roller.svg');
    .no-svg & {
      background-image: url('../images/icon-paint-roller.png');
    }
  }
}

div.three-cols div.theft {

  header.drugs {
    background-image: url('../images/graph-02.svg');
    .no-svg & {
      background-image: url('../images/graph-02.png');
    }
  }

  hgroup::before {
    background-size: 44%;
    background-image: url('../images/icon-notebook.svg');
    .no-svg & {
      background-image: url('../images/icon-notebook.png');
    }
  }
}

div.three-cols div.compensation {

  header.drugs {
    background-image: url('../images/graph-03.svg');
    .no-svg & {
      background-image: url('../images/graph-03.png');
    }
  }
  hgroup::before {
    background-size: 55%;
    background-image: url('../images/icon-clock.svg');
    .no-svg & {
      background-image: url('../images/icon-clock.png');
    }
  }
}
