span.main-bg {
  margin-top: 60px; }

/* ***********************
  basics
*********************** */
header.site_header span.stripe {
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #000;
  z-index: 1500;
  transition: margin 0.5s ease-in-out; }

header.site_header span.main-nav {
  position: absolute;
  left: 0;
  top: 60px; }

header.site_header li,
header.site_header li a {
  display: block;
  float: left;
  clear: left;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%; }

a.site-title {
  display: inline-block;
  text-align: center;
  vertical-align: bottom;
  float: left;
  clear: none;
  height: 60px; }

span.mobile_shader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out; }

/* **********************
  mobile menu OFF:
********************** */
nav.site-nav {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  z-index: 1000;
  transition: height 0.25s ease-in-out; }
  nav.site-nav li a {
    padding: 1.2135em; }

nav.site-nav > span {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%; }

nav.site-nav span.mn,
nav.site-nav span.sn {
  display: block;
  float: left;
  clear: both;
  width: 100%; }

nav.site-nav span.mn {
  padding-top: 60px;
  background-color: #FFF; }

nav.site-nav span.sn {
  padding-bottom: 60px;
  background-color: transparent; }

ul.mainNav,
ul.subNav {
  display: block;
  float: left;
  clear: left;
  width: 100%; }

ul.mainNav li a,
ul.subNav li a {
  background-color: #FFF; }

ul.subNav li a {
  background: #CCC; }

/* **********************
  mobile menu ON:
********************** */
body.main_nav_on nav.site-nav {
  transition: height 0.5s ease-in-out; }

body.main_nav_on body > div.site {
  position: fixed; }

body.main_nav_on span.mobile_shader {
  background-color: rgba(0, 0, 0, 0.6);
  transition: background-color 0.4s ease-in-out; }

/* **********************
  masthead HIDDEN:
********************** */
body.hiddenMasthead header.site_header span.stripe {
  margin-top: -60px; }

span.toggle_menu {
  display: block;
  float: left;
  clear: none;
  width: 60px;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.3);
  color: #FFF; }
  span.toggle_menu .line {
    position: relative;
    background: #FFF;
    height: 3px;
    width: 32px;
    border-radius: 3px;
    margin: 6px 14px;
    transition: all 0.4s ease-in-out; }
  span.toggle_menu .line-1 {
    margin-top: 20px; }
  span.toggle_menu a.toggle {
    position: absolute;
    left: 0;
    top: 0;
    width: 60px;
    height: 60px;
    background-color: transparent;
    cursor: pointer; }

body.main_nav_on span.toggle_menu .line-1,
body.main_nav_on span.toggle_menu .line-2,
body.main_nav_on span.toggle_menu .line-3 {
  position: relative;
  left: 0;
  transition: all 0.4s ease-in-out; }

body.main_nav_on span.toggle_menu .line-2 {
  background: transparent; }

body.main_nav_on span.toggle_menu .line-1, body.main_nav_on span.toggle_menu .line-3 {
  width: 29px; }

body.main_nav_on span.toggle_menu .line-1 {
  margin: 21px 0 0 16.5px;
  transform-origin: 10% 100%;
  transform: rotateZ(45deg); }

body.main_nav_on span.toggle_menu .line-3 {
  margin: -1.5px 0 0 19px;
  transform-origin: 10% 100%;
  transform: rotateZ(-45deg); }

@media (min-width: 45em) {
  span.main-bg {
    margin-top: 0; }
  /* ***********************
  basics
*********************** */
  header.site_header {
    min-height: 6.875em; }
    header.site_header span.toggle_menu {
      display: none !important;
      visibility: hidden; }
    header.site_header li,
    header.site_header li a {
      width: auto;
      clear: none; }
  header.site_header span.stripe {
    z-index: auto;
    display: block;
    width: auto;
    height: 0;
    background-color: #CCC;
    position: relative;
    transition: margin 0;
    max-width: 75em;
    margin-left: auto;
    margin-right: auto; }
    header.site_header span.stripe::after {
      clear: both;
      content: "";
      display: block; }
  a.site-title {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    background-color: transparent;
    width: 7.5em;
    height: 7.5em;
