    background-repeat: no-repeat;
    background-position: 10px 2px;
    background-size: 90%; }
    .no-svg a.site-title {
      background-image: url(../images/wpca-logo.png); }
    a.site-title h1 {
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px; }
  nav.site-nav {
    display: block;
    position: relative;
    float: left;
    clear: none;
    top: 0;
    width: 100%;
    background: transparent;
    z-index: 0;
    min-height: 6.875em;
    border-bottom: none;
    overflow: visible; }
  nav.site-nav > span {
    top: 0; }
  nav.site-nav span.mn,
  nav.site-nav span.sn {
    float: none;
    clear: none;
    width: 100%;
    padding: 0;
    background-color: #000; }
    nav.site-nav span.mn > span,
    nav.site-nav span.sn > span {
      display: block;
