      margin-left: auto;
      margin-right: auto; }
      nav.site-nav span.mn > span::after,
      nav.site-nav span.sn > span::after {
        clear: both;
        content: "";
        display: block; }
  nav.site-nav span.mn {
    background-color: transparent;
    padding-top: 3em; }
  ul.mainNav li a,
  ul.subNav li a {
    color: #FFF;
