      color: #78b6e4; }
    .no-touchevents ul.mainNav li a:hover, .no-touchevents
    ul.subNav li a:hover {
      color: #78b6e4; }
  ul.subNav {
    float: right;
    background-color: transparent;
    width: auto;
    display: block; }
  ul.mainNav li a {
    font-size: larger;
    padding: 0 0.625em 0.5em;
    line-height: 1em; }
  ul.subNav li a {
    padding: 0 0.91013em;
    line-height: 2.8125em; }
  /* **********************
  masthead HIDDEN:
********************** */
  body.hiddenMasthead header.site_header span.stripe {
    margin-top: 0; } }

@media (min-width: 71.25em) {
  header.site_header {
    font-size: 1.2em; }
