div.slide-stripe {
  display: block;
  float: left;
  clear: both;
  width: 100%;
  background-color: #FCFCFC;
}
div.slide-stripe > span {
  display: block;
  float: left;
  clear: none;
  overflow: hidden;
  position:relative;
  width: 50%; // this is overriding my inline style
}

div.slide-stripe > span img.spacer {
  display: block;
  float: left;
  clear: none;
  width: 100%;
  background-color: #000;
}
div.slide-stripe > span img.photo {
  position: absolute;
  min-width: 100%;

  left: 0%;
  top: 0%;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  transition: all 0.2s ease;
}

div.slide-stripe > span:hover img.photo {
  opacity: 0.8;
  left: -5%;
  top: -5%;
  min-width: 110%;
}


