
.content ul.contractors-executive {
  border-bottom: 1px solid #000;
  margin-bottom: 5em;
}

.contractor-page {
  .specialties,
  .services-offered,
  .featured-projects,
  .executives,
  .address {
    display: block;
    margin-bottom: 0.5em;

    h3 {
      margin-bottom: 0.25em;
      border-bottom: 1px solid $color-wpca-blue;
    }
    h4,
    h5 {
      margin-bottom: 0;
    }
    h5 {
      font-size: 1em;
      font-weight: normal;
      line-height: 1.5em;
    }
  }

  .executives {
    text-align: center;
  }

  .address {
    background: transparent;
    text-align: center;

    @media($medium-breakpoint) {
      text-align: left;
    }
  }

  .info-left {
    // @include span-columns(12);
    display: block;

    @media($medium-breakpoint) {
      @include span-columns(4);
    }

    img {
      display: block;
      margin: 0 auto;
    }
  }
  .main {
    // @include span-columns(12);
    display: block;

    @media($medium-breakpoint) {
      @include span-columns(8);
    }
  }
}
