.contractor-list {
  h1 {
    text-align: center;
  }
}

.contractors-executive,
.contractors {
  @include outer-container;
  display: block;
  position: relative;

  li {
    @include span-columns(12);
    text-align: center;
    margin-bottom: 1.75em;
    padding: 1em;
    border-radius: 0.5em;
    background: $white;

    @include media(min-width $width-small max-width $large_size) {
      @include span-columns(6);
      @include omega(2n);
    }

    @media (min-width: $large_size) {
      @include span-columns(4);
      @include omega(3n);
    }
  }

  h5 {
    margin-bottom: 0;
  }

  a {
    display: block;
  }

  img {
    width: 80%;
    max-width: pem(200);
  }
}

.link-container {
  display: block;
  padding: 0 3em;

  .profile-link {
    background: $color-wpca-blue;
    text-decoration: none;
    color: $white;
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    transition: background 0.2s;

    &:hover {
      background: darken($color-wpca-blue, 20%);
    }
  }
}
